import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import * as S from "./styles";
import { TagLink } from "../Links";

import StickerPng from "../../../images/telbook.png";
import FolderPng from "../../../images/folder.png";
import { CategoryImgMap } from "../../theme/Constants";

declare type MdxQuery = {
  allMdx: {
    categories: string[];
    tags: string[];
  };
};

const SideBar = (): JSX.Element => {
  // get all categories and tags data
  const listdata = useStaticQuery<MdxQuery>(graphql`
    query CategoriesQuery {
      allMdx {
        categories: distinct(field: frontmatter___category)
        tags: distinct(field: frontmatter___tags)
      }
    }
  `);

  return (
    <S.Wrapper>
      <S.TitleRow>
        <S.TitleIcon src={FolderPng} alt="分类" />
        <S.TitleText>分类</S.TitleText>
      </S.TitleRow>
      <S.BlockCol>
        {listdata.allMdx.categories.map((v) => (
          <S.CategoryLink to={`/category/${v}`} key={v}>
            <S.CateIcon src={CategoryImgMap[v]} alt={v} />
            {v}
          </S.CategoryLink>
        ))}
      </S.BlockCol>

      <S.TitleRow>
        <S.TitleIcon src={StickerPng} alt="标签" />
        <S.TitleText>标签</S.TitleText>
      </S.TitleRow>
      <S.BlockRow>
        {listdata.allMdx.tags.map((v) => (
          <TagLink to={`/tag/${v}`} key={v}>
            {v}
          </TagLink>
        ))}
      </S.BlockRow>
    </S.Wrapper>
  );
};

export default SideBar;
