import React, { useCallback, useEffect, useRef, useState } from "react";

const useBacktopScroll = (): [
  scrollRef: React.RefObject<HTMLDivElement>,
  shouldShow: boolean
] => {
  const scrollElementRef = useRef<HTMLDivElement>(null);

  const [shouldShow, setShouldShow] = useState<boolean>(false);

  const checkScrollState = useCallback(() => {
    // console.log('执行scroll检查', !!scrollElementRef.current)
    if (scrollElementRef.current) {
      const { top } = scrollElementRef.current.getBoundingClientRect();
      if (top <= -800) {
        setShouldShow(true);
      } else {
        setShouldShow(false);
      }
    }
  }, []);

  useEffect(() => {
    checkScrollState();

    window.addEventListener("scroll", checkScrollState);

    return () => {
      window.removeEventListener("scroll", checkScrollState);
    };
  }, [checkScrollState]);

  return [scrollElementRef, shouldShow];
};

export default useBacktopScroll;
