import styled, { css } from "styled-components";

import { GatsbyImage } from "gatsby-plugin-image";

import { Body, constants } from "../../theme";

type CardStyleProps = { hero?: boolean };

export const Wrapper = styled.div<CardStyleProps>`
  display: grid;
  cursor: pointer;
  grid-gap: 24px;
  box-shadow: 4px 4px #ddd;
  padding: 24px;
  background-color: white;
  transition: all 0.4s ease;

  ${({ hero }) =>
    hero &&
    css`
      @media (min-width: ${constants.breakpoints.lg}) {
        grid-column: span 2;
        grid-template-columns: 6fr 4fr;
      }
    `}

  :hover {
    transform: translate(-4px, -4px);
    box-shadow: 8px 8px #ddd;
  }

  @media (max-width: ${constants.breakpoints.lg}) {
    padding: 16px;
    grid-gap: 16px;
  }

  @media (max-width: ${constants.breakpoints.sm}) {
    padding: 16px;
    grid-gap: 16px;
  }
`;

export const Excerpt = styled(Body)<CardStyleProps>`
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  color: var(--color-grey-700);

  ${({ hero }) =>
    hero &&
    css`
      @media (min-width: ${constants.breakpoints.lg}) {
        -webkit-line-clamp: 5;
      }
    `}
`;

export const Meta = styled.div`
  display: grid;
  grid-gap: 12px;

  align-items: flex-start;
  align-content: start;
`;

export const Details = styled.div<CardStyleProps>`
  display: grid;

  ${({ hero }) =>
    hero &&
    css`
      @media (min-width: ${constants.breakpoints.lg}) {
        align-content: space-between;
      }
    `}
`;

export const CoverWrapper = styled.div`
  position: relative;
  width: 100%;
  padding-top: 60%;
`;

export const Cover = styled(GatsbyImage)`
  position: absolute !important;
  top: 0;
  width: 100%;
  left: 0;
  height: 100%;
`;
