import React, { useMemo } from "react";
import styled from "styled-components";

import { SEO, Types, PostTemplateProps } from "gatsby-theme-advanced";

import Layout from "../../../layouts";
import Article from "../../../components/Article";
import AuthorSegment from "../../../components/AuthorSegment";
import RelatedPosts from "../../../components/RelatedPosts";
import Breadcrumb, { BreadcrumbType } from "../../../components/Breadcrumb";
import { breakpoints } from "../../../theme/Constants";
import { LikeBoxWrapper } from "../../../components/Article/Spacing";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;

  @media (max-width: ${breakpoints.sm}) {
    gap: 24px;
  }
`;

const PostTemplate = ({
  data,
  pageContext,
}: PostTemplateProps): JSX.Element => {
  const post = Types.queryIntoPost(data);

  const breadcrumbs = useMemo<BreadcrumbType[]>(() => {
    const arr: BreadcrumbType[] = [];
    if (post.category) {
      arr.push({
        label: post.category,
        location: `/category/${post.category}`,
      });
    }
    arr.push({ label: post.title });
    return arr;
  }, [post]);

  return (
    <Layout showSidebar>
      <Wrapper>
        <SEO post={post} />

        <LikeBoxWrapper>
          <Breadcrumb breadcrumbs={breadcrumbs} />
        </LikeBoxWrapper>

        <Article post={post} />

        <AuthorSegment />
        <LikeBoxWrapper>
          <RelatedPosts
            list={pageContext.relatedPosts.map(Types.jsonPostIntoPost)}
          />
        </LikeBoxWrapper>
      </Wrapper>
    </Layout>
  );
};

export default PostTemplate;
