import React from "react";
import styled, { css } from "styled-components";

import { GatsbyImage, IGatsbyImageData } from "gatsby-plugin-image";

import SharedImage, { ImageShadow } from "../shared/Image";
import { styles } from "../../theme";

import { ARTICLE_WIDTH } from "./Spacing";
import { breakpoints } from "../../theme/Constants";

export const ArticleImageSpacing = css`
  /* Change width and padding based on screen size */
  @media (min-width: ${ARTICLE_WIDTH}) {
    padding: 0 16px;

    /* Slightly round image corners */
    & > img {
      border-radius: 2px;
    }

    max-width: ${ARTICLE_WIDTH};
  }
`;

type FigureProps = {
  cover?: boolean;
};

const Figure = styled.figure<FigureProps>`
  display: grid;
  justify-items: center;

  width: 100%;

  /* Apply spacing depending on type */
  ${({ cover }) => (!cover ? ArticleImageSpacing : "")}
`;

const Img = styled(SharedImage)`
  max-width: 100%;
`;

const StyledGatsbyImage = styled(GatsbyImage)`
  width: 100%;
  background-color: #eee;

  /* Limit cover image heights */
  max-height: 500px;
  min-height: 150px;

  & img {
    ${ImageShadow}
  }
`;

export const FigCaptionCss = css`
  ${styles.Caption}

  min-width: 155px;
  max-width: 80%;
  display: inline-block;
  text-align: center;
  padding: 12px;
  margin: 8px auto 0;
  border-bottom: 1px solid #eee;
  color: var(--color-grey-500);

  @media (max-width: ${breakpoints.sm}) {
    padding: 8px;
    margin-top: 0;
  }
`;

const FigCaptionCoverCss = css`
  border-bottom: none;
  padding-bottom: 0;
`;

type FigureCaptionProps = {
  cover?: boolean;
};

const FigCaption = styled.figcaption<FigureCaptionProps>`
  ${FigCaptionCss}

  ${(props) => (props.cover ? FigCaptionCoverCss : "")}
`;

type ImageProps = {
  src?: string;
  title?: string;
  alt: string;
};

// Used by MDX Image component (without local picture)
export const MdxImage = ({ src, alt, title }: ImageProps): JSX.Element => (
  <Figure>
    <Img src={src} alt={alt} />
    {(title || alt) && <FigCaption>{title || alt}</FigCaption>}
  </Figure>
);

type CoverImageProps = {
  image: IGatsbyImageData;
  alt: string;
  objectFit?: React.CSSProperties["objectFit"];
};

// Used by article cover image
export const CoverImage = ({
  image,
  alt,
  objectFit,
}: CoverImageProps): JSX.Element => (
  <Figure cover>
    <StyledGatsbyImage image={image} objectFit={objectFit} alt={alt} />
    <FigCaption cover>{alt}</FigCaption>
  </Figure>
);

export default MdxImage;
