import React from "react";
import styled from "styled-components";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { Types } from "gatsby-theme-advanced";

import { H1 } from "../../theme";
import ArticleInfo from "../ArticleInfo";
// import { CoverImage } from "./Image";
import { LikeBoxWrapper } from "./Spacing";
import { breakpoints } from "../../theme/Constants";

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  gap: 24px;

  @media (max-width: ${breakpoints.sm}) {
    gap: 16px;
  }
`;

const Cover = styled.div`
  position: relative;
  width: 100%;
  padding-top: 50%;
  margin-bottom: 24px;

  @media (max-width: ${breakpoints.sm}) {
    margin-bottom: 12px;
  }
`;

const CoverImage = styled(GatsbyImage)`
  position: absolute !important;
  top: 0;
  width: 100%;
  left: 0;
  height: 100%;
  border: 8px solid #fff;

  @media (max-width: ${breakpoints.sm}) {
    border: none;
  }
`;

type ArticleIntroProps = {
  post: Types.Post;
};

const ArticleIntro = ({ post }: ArticleIntroProps): JSX.Element => {
  if (!post.coverImg)
    throw Error(
      "ArticleIntro: Failed to render ArticleIntro without cover image."
    );

  const imgData = getImage(post.coverImg);

  if (!imgData)
    throw Error("ArticleIntro: Failed to getImage from post.coverImg.");

  return (
    <Wrapper>
      <LikeBoxWrapper>
        <Cover>
          <CoverImage
            image={imgData}
            objectFit="cover"
            alt={post.coverImageAlt}
          />
        </Cover>
      </LikeBoxWrapper>

      <LikeBoxWrapper>
        <H1>{post.title}</H1>
      </LikeBoxWrapper>
      {/* <Body>{post.excerpt}</Body> */}
      <LikeBoxWrapper>
        <ArticleInfo post={post} showMore />
      </LikeBoxWrapper>
    </Wrapper>
  );
};

export default ArticleIntro;
