import React from "react";
import styled, { createGlobalStyle, css } from "styled-components";

import { styles } from "../../../theme";
import { SIDE_PADDING } from "../Spacing";
import CopyImg from "../../../../images/copy.png";

const CODE_CONTENT_WIDTH = "920px";
const CODE_BLOCK_SIZE = `calc(${CODE_CONTENT_WIDTH} + 2 * ${SIDE_PADDING})`;

const CopyBtn = styled.img.attrs({ src: CopyImg, alt: "复制", title: "复制" })`
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: auto;
  margin: 5px;
  cursor: pointer;
  z-index: 1;
  transition: transform 0.2s ease-in-out;

  &:hover {
    transform: scale(1.1);
  }
`;

export const GlobalCodeStyle = createGlobalStyle`${css`
  .gatsby-highlight {
    position: relative;
    width: 100%;
    padding: 0;
  }

  /* only top-level code needs padding */
  .top-render-box > .gatsby-highlight {
    @media (min-width: ${CODE_BLOCK_SIZE}) {
      padding: 0 16px;

      max-width: ${CODE_BLOCK_SIZE};

      > ${CopyBtn} {
        margin: 5px 21px 0 0;
      }
    }
  }

  /* support inlineCode styling */
  :not(pre) > code[class*="language-"].inline-code-class {
    padding: 2px 6px;
    margin: 0 2px;
    background-color: #e7e7e7;
    color: var(--color-grey-700);
    text-shadow: none;
    border-radius: 2px;
  }
`}`;

export const StyledPre = styled.pre`
  ${styles.Code}

  width: 100%;
  max-width: 100%;
  max-height: 90vh;

  margin: 0 !important;

  /* Set border radius depending on element state */
  border-radius: 0px !important;

  @media (min-width: ${CODE_BLOCK_SIZE}) {
    border-radius: 4px !important;

    max-width: ${CODE_BLOCK_SIZE};
  }
`;

export const Pre = ({ className, children }: CodeProps): JSX.Element => (
  <>
    <CopyBtn />
    <StyledPre className={className}>{children}</StyledPre>
  </>
);

export const StyledCode = styled.code`
  ${styles.Code}

  width: 100%;
  max-width: 100%;
`;

type CodeProps = { className?: string; children?: React.ReactChildren };

export const Code = ({ className, children }: CodeProps): JSX.Element => (
  <StyledCode className={`${className || ""} inline-code-class`} tabIndex={0}>
    {children}
  </StyledCode>
);

export const InlineCode = undefined; // Default style works great
