import React from "react";
import { css, createGlobalStyle } from "styled-components";

import {
  MdxImage as ArticleImage,
  ArticleImageSpacing,
  FigCaptionCss,
} from "../Image";
import SharedImage from "../../shared/Image";
import { AnimatedLink } from "../../Links";
import Separator from "../../shared/Separator";

import { ExtendingWrapper } from "../Spacing";

export const Break = (): JSX.Element => (
  <ExtendingWrapper>
    <Separator />
  </ExtendingWrapper>
);

interface LinkProps extends React.HTMLProps<HTMLAnchorElement> {
  activeClassName?: string;
  to: string;
}

export const Link = ({
  children,
  className,
  activeClassName,
  to,
  href,
  target,
  rel,
  style,
}: LinkProps): JSX.Element => {
  // Let gatsby-remark-images handle its links
  if (className === "gatsby-resp-image-link")
    return (
      <a
        className={className}
        href={href}
        target={target}
        rel={rel}
        style={style}
      >
        {children}
      </a>
    );

  return (
    <AnimatedLink
      className={className}
      activeClassName={activeClassName}
      to={to}
      href={href}
      target={target}
    >
      {children}
    </AnimatedLink>
  );
};

// local image processed by [gatsby-plugin-image], only global css useable
export const GlobalGatsbyImageStyle = createGlobalStyle`${css`
  .gatsby-resp-image-figure {
    width: 100%;
    text-align: center;
  }

  .gatsby-resp-image-wrapper {
    position: relative;
  }

  .gatsby-resp-image-link {
    position: relative;
  }

  .gatsby-resp-image-figcaption {
    ${FigCaptionCss}
  }

  /* top-level image remove padding */
  .top-render-box > .gatsby-resp-image-wrapper {
    padding: 0;
  }

  /* only top-level figure needs padding */
  .top-render-box > .gatsby-resp-image-figure {
    ${ArticleImageSpacing}
  }
`}`;

export const Image = ({
  src,
  alt,
  className,
  title,
  srcSet,
  sizes,
  loading,
  style,
}: React.ImgHTMLAttributes<HTMLImageElement>): JSX.Element => {
  // local image
  if (className === "gatsby-resp-image-image") {
    return (
      <SharedImage
        src={src}
        alt={alt}
        title={title}
        srcSet={srcSet}
        sizes={sizes}
        loading={loading}
        style={style}
        className={className} // Make sure to set the className otherwise the plugin crashes
      />
    );
  }

  return <ArticleImage src={src} alt={alt || ""} title={title} />;
};
