import React from "react";
import { format } from "date-fns";

import { Types } from "gatsby-theme-advanced";

import * as S from "./styles";
import { TagLink } from "../Links";

import BookPng from "../../../images/book.png";
import TimerPng from "../../../images/timer.png";

type ArticleInfoProps = {
  post: Types.Post;
  showMore?: boolean;
};

const ArticleInfo = ({ post, showMore }: ArticleInfoProps): JSX.Element => {
  const categoryUrl = post.category ? `/category/${post.category}` : undefined;

  const publicationDate = format(post.datePublished, "yyyy年MM月dd日 HH:mm");

  const timeToRead = `阅读${post.timeToRead * 5}分钟`;

  // const infoCaption = `${publicationDate}${timeToRead}`;

  // Display the first 2 tags
  const tags = showMore ? post.tags : post.tags?.slice(0, 2);
  const tagLinks = tags?.map((tag) => (
    <TagLink key={tag} to={`/tag/${tag}`} onClick={(e) => e.stopPropagation()}>
      # {tag}
    </TagLink>
  ));

  return (
    <S.Wrapper>
      <S.InfoGrid>
        {categoryUrl && (
          <S.CategoryLink to={categoryUrl}>{post.category}</S.CategoryLink>
        )}

        <S.CaptionDiv>
          <S.CaptionImg src={BookPng} alt="book" />
          <S.Caption>{timeToRead}</S.Caption>
        </S.CaptionDiv>

        {showMore ? (
          <S.CaptionDiv>
            <S.CaptionImg src={TimerPng} alt="timer" />
            <S.Caption>{publicationDate}</S.Caption>
          </S.CaptionDiv>
        ) : null}
      </S.InfoGrid>

      <S.TagGrid>{tagLinks}</S.TagGrid>
    </S.Wrapper>
  );
};

export default ArticleInfo;
