import React from "react";

import styled from "styled-components";

import { TransparentLink } from "../Links";
import ArrowPng from "../../../images/arrow.png";

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const BreadLink = styled(TransparentLink)`
  color: var(--color-accent);
  font-size: 14px;
  width: fit-content;
  margin-right: 3px;
`;

const BreadText = styled.span`
  color: var(--color-grey-700);
  font-size: 14px;
  width: fit-content;
  margin-right: 3px;
`;

const Arrow = styled.img`
  height: 9px;
  margin: 0 8px;
  width: 6px;
`;

export type BreadcrumbType = {
  label: string;
  location?: string;
};

type BreadcrumbProps = {
  defaultLabel?: string;
  breadcrumbs?: BreadcrumbType[];
};

const Breadcrumb = ({
  defaultLabel = "首页",
  breadcrumbs = [],
}: BreadcrumbProps): JSX.Element => (
  <Wrapper>
    <BreadLink to="/">{defaultLabel}</BreadLink>

    {breadcrumbs.map((v) => (
      <React.Fragment key={v.label}>
        <Arrow src={ArrowPng} alt=">" />
        {v.location ? (
          <BreadLink to={v.location}>{v.label}</BreadLink>
        ) : (
          <BreadText>{v.label}</BreadText>
        )}
      </React.Fragment>
    ))}
  </Wrapper>
);

export default Breadcrumb;
