import React from "react";
import { withPrefix } from "gatsby";

import LogoPng from "../../../images/logo.png";
import ExternalPng from "../../../images/external.png";

import * as S from "./style";

export const NavigationHeader = (): JSX.Element => (
  <S.HomeButton to="/">
    <S.HomeIcon src={LogoPng} alt="logo" />
    <S.SiteInfo>
      <S.SiteText>程序员LEON</S.SiteText>
      <S.SiteSubTitle>像素主题</S.SiteSubTitle>
    </S.SiteInfo>
  </S.HomeButton>
);

const Navigation = (): JSX.Element => (
  <S.Wrapper>
    <NavigationHeader />

    <S.NavRight>
      <S.SearchForm method="get" action={withPrefix("/search")}>
        <input type="text" name="k" placeholder="输入关键字" />
        <input type="submit" value="" alt="搜索" title="搜索" />
      </S.SearchForm>
      <S.NavButton to="/">文章</S.NavButton>
      <S.NavButton to="/about">关于我</S.NavButton>
      <S.NavButton to="https://github.com/missmess" target="_blank">
        <S.ButtonRow>
          <span>GITHUB</span>
          <img src={ExternalPng} alt="" />
        </S.ButtonRow>
      </S.NavButton>
      <S.NavButton to="https://gitee.com/missmess" target="_blank">
        <S.ButtonRow>
          <span>GITEE</span>
          <img src={ExternalPng} alt="" />
        </S.ButtonRow>
      </S.NavButton>
    </S.NavRight>
  </S.Wrapper>
);

export default Navigation;
