import React from "react";
import styled from "styled-components";

import { Types, useConfig } from "gatsby-theme-advanced";

import { IconLink } from "../Links";
import GithubPng from "../../../images/github.png";
import GiteePng from "../../../images/gitee.png";
import MailPng from "../../../images/mail.png";
import CsdnPng from "../../../images/csdn.png";

type SiteConfig = Types.SiteConfig;

const LinkGrid = styled.div`
  display: flex;
  gap: 24px;
`;

// Utilities
const renderLink = (url: string, label: string, Icon: string): JSX.Element => (
  <IconLink to={url} ariaLabel={label} target="_blank">
    <img src={Icon} width={32} height={32} alt={label} />
  </IconLink>
);

const renderGitHubLink = (config: Readonly<SiteConfig>): JSX.Element | null => {
  const userName = config.user?.github;

  if (!userName) return null;

  const url = `https://github.com/${userName}`;
  return renderLink(url, "GitHub账户", GithubPng);
};

const renderGiteeLink = (config: Readonly<SiteConfig>): JSX.Element | null => {
  const userName = config.user?.gitee;

  if (!userName) return null;

  const url = `https://gitee.com/${userName}`;
  return renderLink(url, "Gitee账户", GiteePng);
};

const renderCsdnLink = (): JSX.Element | null => {
  const url = `https://blog.csdn.net/wangleixhlm`;
  return renderLink(url, "Csdn账户", CsdnPng);
};

const renderEmailLink = (config: Readonly<SiteConfig>): JSX.Element => {
  const url = `mailto:${config.user?.email || ""}`;
  return renderLink(url, "邮箱", MailPng);
};

type IconLinksProps = {
  className?: string;
};

const UserLinks = ({ className }: IconLinksProps): JSX.Element | null => {
  const config = useConfig();

  if (!config.user) return null;

  return (
    <LinkGrid className={className}>
      {renderGitHubLink(config)}
      {renderGiteeLink(config)}
      {renderEmailLink(config)}
      {renderCsdnLink()}
    </LinkGrid>
  );
};

export default UserLinks;
