import React from "react";
import styled from "styled-components";

import { Types } from "gatsby-theme-advanced";

import { MDXRenderer } from "gatsby-plugin-mdx";
import MDXTheme from "./MdxTheme";

import { WrapperCss, BoxCss } from "./Spacing";
import { breakpoints } from "../../theme/Constants";

export const RenderBox = styled.article`
  ${BoxCss}

  color: var(--color-grey-700);
  background-color: white;
  border-radius: 8px;

  overflow: hidden;
  margin-bottom: 32px;

  @media (max-width: ${breakpoints.sm}) {
    border-radius: 0;
  }

  /* Center and space child elements */
  & > * {
    margin-bottom: 22px;
    margin-right: auto;
    margin-left: auto;

    @media (max-width: ${breakpoints.sm}) {
      margin-bottom: 10px;
    }
  }

  /* Apply generic width rules to everything except images, separators and containers */
  & > *:not(figure, div, hr) {
    ${WrapperCss}
  }

  /* Apply margins for headings */
  & > h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: black;
    margin-top: 32px;
    margin-bottom: 24px;
  }

  /* Remove top margin from the first child */
  & > *:first-child {
    margin-top: 0px;
  }
`;

type RenderProps = {
  post: Types.Post;
};

const Render = ({ post }: RenderProps): JSX.Element => {
  if (!post.body)
    throw Error(
      `Render: post data doesn't contain MDX body for rendering. Aborting. Post slug: ${post.slug}`
    );

  return (
    <RenderBox className="top-render-box">
      <MDXTheme slug={post.slug}>
        <MDXRenderer>{post.body}</MDXRenderer>
      </MDXTheme>
    </RenderBox>
  );
};

export default Render;
