import styled, { css } from "styled-components";
import { BaseLink } from "./BaseLink";

type LinkStyleProps = {
  activeClassName?: string;
};

const TransparentCSS = css`
  text-decoration: none;
  color: inherit;

  width: 100%;
`;

export const IconCSS = css`
  color: var(--color-grey-700);
  font-size: 0;

  &:hover {
    color: var(--color-primary);
  }
`;

const PrimaryCSS = css`
  text-decoration: none;
  color: var(--color-primary);

  &:hover {
    color: var(--color-primary);
  }
`;

const AnimatedCSS = css<LinkStyleProps>`
  position: relative;
  text-decoration: none;
  color: var(--color-primary);

  background-color: inherit;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px;
    width: 100%;
    height: 0;

    border-bottom: 2px dotted var(--color-primary);

    transition: transform 300ms ease;
    transform: scaleX(0);
    transform-origin: 0% 100%;
  }

  &:hover {
    color: var(--color-primary);
  }
  &:hover::after {
    transform: scaleX(1);
  }

  &.${(props) => props.activeClassName} {
    &:after {
      transform: scaleX(1);
    }
  }
`;

const HeadingCss = css`
  text-decoration: none;
  color: inherit;

  &:hover::after {
    content: "#";
    display: inline-block;
    position: absolute;
    padding-left: 8px;

    color: var(--color-primary);
  }
`;

const TagCss = css`
  background-color: #fff;
  box-shadow: 2px 2px #ddd;
  color: var(--color-grey-800);
  display: inline-block;
  padding: 2px 6px;
  text-decoration: none;

  font-size: 14px;
  line-height: 18px;

  :hover {
    background: #000;
    color: #fff;
  }
`;

export const AnimatedLink = styled(BaseLink).attrs(
  ({ activeClassName }: LinkStyleProps) => ({
    activeClassName: activeClassName || "gatsby-active-link",
  })
)`
  ${AnimatedCSS}
`;

export const TransparentLink = styled(BaseLink)`
  ${TransparentCSS}
`;

export const PrimaryLink = styled(BaseLink)`
  ${PrimaryCSS}
`;

export const IconLink = styled(BaseLink)`
  ${IconCSS}
`;

export const HeadingLink = styled(BaseLink)`
  ${HeadingCss}
`;

export const TagLink = styled(BaseLink)`
  ${TagCss}
`;
